.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  padding: 13px 0 0;

  @media screen and (max-width: 1023px) {
    padding: 28px 0 0;
  }

  @media screen and (max-width: 767px) {
    padding: 0;
    width: 100%;
  }

  label {
    display: flex;
    align-items: center;
    width: 100%;

    @media screen and (max-width: 767px) {
      align-items: flex-start;
      flex-direction: column;
    }

    input,
    textarea {
      flex-grow: 1;
      max-width: 300px;
      min-height: 48px;
      margin-left: auto;
      padding: 10px;
      border: 1px solid rgba($color-sapphire, 0.2);
      outline: none;
      transition: border 0.3s ease;

      @media screen and (max-width: 767px) {
        max-width: unset;
        width: 100%;
      }

      &:hover {
        border: 1px solid rgba($color-sapphire, 0.5);
      }

      &:focus {
        border: 1px solid rgba($color-sapphire, 0.8);
      }
    }
  }

  &__label {
    @media screen and (max-width: 767px) {
      margin: 0 0 18px 0;
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: 35px;
    color: $color-torch-red-two;
    opacity: 0;
    transition: opacity 0.3s ease;

    @media screen and (max-width: 767px) {
      bottom: 20px;
    }
  }

  &.is-invalid {
    input,
    textarea {
      border-bottom: 1px solid $color-torch-red-two;

      &:focus,
      &:hover {
        border: 1px solid rgba($color-torch-red-two, 0.5);
      }
    }
  }

  &.is-valid {
    input {
      border-bottom: 1px solid $color-green;

      &:focus,
      &:hover {
        border: 1px solid rgba($color-green, 0.5);
      }
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;
    font-size: 12px;

    &.is-invalid {
      color: $color-torch-red-two;
    }

    &.is-valid {
      color: $color-green;
    }
  }
}
